import React, {useState} from 'react'
import './style.css'

const Index = (props) => {
    const {setLoginAccount, loginAccount, admin, setAdmin, users, setUsers, defaultProfPic} = props;

    const [oldPassword, setOldPassword] = useState('')
    const [newPassword, setNewPassword] = useState('')
    const [retypePassword, setRetypePassword] = useState('')


    const handleSaveNewPassword = () => {
        let enPassword = Buffer.from(oldPassword).toString('base64');
        if(loginAccount[0].password === enPassword) {
            if(newPassword){
                if(newPassword === retypePassword) {
                    let enPasswordNewPassword = Buffer.from(newPassword).toString('base64');
                    let enPasswordRetypePassword = Buffer.from(retypePassword).toString('base64');
                    setNewPassword(enPasswordRetypePassword)
                    let findLoginAccount = admin.find((item) => item.id === loginAccount[0].id)
                    let findLoginAccountUsers = users.find((item) => item.account_no === loginAccount[0].account_no)
                    if(findLoginAccount) {
                        setAdmin([...admin], findLoginAccount.password = enPasswordNewPassword)
                        setLoginAccount([...loginAccount], loginAccount[0].password = enPasswordNewPassword)
                        setOldPassword('')
                        setNewPassword('')
                        setRetypePassword('')
                        alert('Password changed succesfully')
                    } 
                    else if (findLoginAccountUsers) {
                        setUsers([...users], findLoginAccountUsers.password = enPasswordNewPassword)
                        setLoginAccount([...loginAccount], loginAccount[0].password = enPasswordNewPassword)
                        setOldPassword('')
                        setNewPassword('')
                        setRetypePassword('')
                        alert('Password changed succesfully')
                    }
                } else {
                    alert(`Password did not match`)
                }
            } else {
                alert('Please type a new password.')
            }
        } else {
            alert('Old Password is incorrect')
        }
    }
    
    const [passwordState, setPasswordState] = useState({
        oldPassword: false,
        newPassword: false,
        retypePassword: false,
    })

    const handleShowPassword = (password, bool) => {
        if(password === 'oldPassword') {
            setPasswordState({oldPassword: bool})
        } else if(password === 'newPassword') {
            setPasswordState({newPassword: bool})
        } else if(password === 'retypePassword') {
            setPasswordState({retypePassword: bool})
        }
    }

    const handleHidePassword = (password, bool) => {
        if(password === 'oldPassword') {
            setPasswordState({oldPassword: bool})
        } else if(password === 'newPassword') {
            setPasswordState({newPassword: bool})
        } else if(password === 'retypePassword') {
            setPasswordState({retypePassword: bool})
        }
    }

    return (
        <div className="container">
            <div className="main-body">
                <div className="row">
                    <div className="col-lg-4">
                    {loginAccount[0] && 
                        <div className="card">
                            <div className="card-body">
                                <div className="d-flex flex-column align-items-center text-center">
                                <img src={loginAccount[0].thumbnail_url || defaultProfPic} alt="Profile Pic" className="rounded-circle p-1 bg-primary" width="110"/>
                                    <div className="mt-3">
                                        <h4>{loginAccount[0] ? loginAccount[0].first_name + " " +loginAccount[0].last_name : null}</h4>
                                        <p className="text-secondary mb-1">{loginAccount[0] ? loginAccount[0].role : null}</p>
                                    </div>
                                </div>
                                {/* <hr className="my-4" />
                                <ul className="list-group list-group-flush">
                                    <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                                        <h6 className="mb-0"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-globe me-2 icon-inline"><circle cx="12" cy="12" r="10"></circle><line x1="2" y1="12" x2="22" y2="12"></line><path d="M12 2a15.3 15.3 0 0 1 4 10 15.3 15.3 0 0 1-4 10 15.3 15.3 0 0 1-4-10 15.3 15.3 0 0 1 4-10z"></path></svg>Website</h6>
                                        <span className="text-secondary">https://bootdey.com</span>
                                    </li>
                                    <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                                        <h6 className="mb-0"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-github me-2 icon-inline"><path d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"></path></svg>Github</h6>
                                        <span className="text-secondary">bootdey</span>
                                    </li>
                                    <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                                        <h6 className="mb-0"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-twitter me-2 icon-inline text-info"><path d="M23 3a10.9 10.9 0 0 1-3.14 1.53 4.48 4.48 0 0 0-7.86 3v1A10.66 10.66 0 0 1 3 4s-4 9 5 13a11.64 11.64 0 0 1-7 2c9 5 20 0 20-11.5a4.5 4.5 0 0 0-.08-.83A7.72 7.72 0 0 0 23 3z"></path></svg>Twitter</h6>
                                        <span className="text-secondary">@bootdey</span>
                                    </li>
                                    <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                                        <h6 className="mb-0"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-instagram me-2 icon-inline text-danger"><rect x="2" y="2" width="20" height="20" rx="5" ry="5"></rect><path d="M16 11.37A4 4 0 1 1 12.63 8 4 4 0 0 1 16 11.37z"></path><line x1="17.5" y1="6.5" x2="17.51" y2="6.5"></line></svg>Instagram</h6>
                                        <span className="text-secondary">bootdey</span>
                                    </li>
                                    <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                                        <h6 className="mb-0"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-facebook me-2 icon-inline text-primary"><path d="M18 2h-3a5 5 0 0 0-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 0 1 1-1h3z"></path></svg>Facebook</h6>
                                        <span className="text-secondary">bootdey</span>
                                    </li>
                                </ul> */}
                            </div>
                        </div>}
                    </div>
                    <div className="col-lg-8 user-info-details">
                        <div className="card">
                        {loginAccount[0] && 
                            <div className="card-body">
                                <div className="row mb-3">
                                    <div className="col-sm-3">
                                        <h6 className="mb-0">{loginAccount[0].role === 'admin' ? 'ID Number' : 'Client Number'}</h6>
                                    </div>
                                    <div className="col-sm-9 text-secondary">
                                        <input type="text" className="form-control" value={loginAccount[0].id || loginAccount[0].account_no} readOnly/>
                                    </div>
                                </div>
                                <div className="row mb-3">
                                    <div className="col-sm-3">
                                        <h6 className="mb-0">First Name</h6>
                                    </div>
                                    <div className="col-sm-9 text-secondary">
                                        <input type="text" className="form-control" value={loginAccount[0].first_name} readOnly/>
                                    </div>
                                </div>
                                <div className="row mb-3">
                                    <div className="col-sm-3">
                                        <h6 className="mb-0">Last Name</h6>
                                    </div>
                                    <div className="col-sm-9 text-secondary">
                                        <input type="text" className="form-control" value={loginAccount[0].last_name} readOnly/>
                                    </div>
                                </div>
                                <div className="row mb-3">
                                    <div className="col-sm-3">
                                        <h6 className="mb-0">Email</h6>
                                    </div>
                                    <div className="col-sm-9 text-secondary">
                                        <input type="text" className="form-control" value={loginAccount[0].email} readOnly/>
                                    </div>
                                </div>
                                <div className="row mb-3">
                                    <div className="col-sm-3">
                                        <h6 className="mb-0">Mobile</h6>
                                    </div>
                                    <div className="col-sm-9 text-secondary">
                                        <input type="text" className="form-control" value={loginAccount[0].mobile_no} readOnly/>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-sm-3"></div>
                                    <div className="col-sm-9 text-secondary">
                                        <input type="button" className="btn btn-primary px-4" value="Change Password" data-bs-toggle="modal" data-bs-target="#exampleModal"/>
                                    </div>
                                </div> 
                            </div>
                        }
                        </div>
                    </div>

                    {/* Modal for Change Password */}
                    <div className="modal fade" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                            <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">Change Password</h5>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>         
                            </div>

                            <div className="modal-body">
                                <div className="form-floating mb-3">
                                    <input type={passwordState.oldPassword? 'text' : 'password'}  className="form-control" id="floatingOldPassword" placeholder="Old Password" onChange={(e) => setOldPassword(e.target.value)} value={oldPassword} />
                                    <label>Old Password</label>
                                    <button className="btn btn-primary show-hide-password-btn" onMouseDown={() => handleShowPassword('oldPassword', true)} onMouseUp={() => handleHidePassword('oldPassword', false)}>{passwordState.oldPassword? 'Hide old password' : 'Show old password'}</button>
                                </div>  
                                <div className="form-floating mb-3">
                                    <input type={passwordState.newPassword? 'text' : 'password'} className="form-control" id="floatingNewPassword" placeholder="New Password" onChange={(e) => setNewPassword(e.target.value)} value={newPassword}/>
                                    <button className="btn btn-primary show-hide-password-btn" onMouseDown={() => handleShowPassword('newPassword', true)} onMouseUp={() => handleHidePassword('newPassword', false)}>{passwordState.newPassword? 'Hide old password' : 'Show old password'}</button>
                                    <label>New Password</label>
                                </div> 
                                <div className="form-floating mb-3">
                                    <input type={passwordState.retypePassword? 'text' : 'password'} className="form-control" id="floatingRetypePassword" placeholder="Retype Password" onChange={(e) =>setRetypePassword(e.target.value)} value={retypePassword}/>
                                    <button className="btn btn-primary show-hide-password-btn" onMouseDown={() => handleShowPassword('retypePassword', true)} onMouseUp={() => handleHidePassword('retypePassword', false)}>{passwordState.retypePassword? 'Hide old password' : 'Show old password'}</button>
                                    <label>Retype Password</label>
                                </div> 
                            </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-primary" data-bs-dismiss={'modal'} data-bs-toggle={"modal"} data-bs-target={"#ModalForgotPassword"} onClick={() => handleSaveNewPassword()}>Save</button>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default Index
